</template>

<script>
export default {
    name: 'Layout',
    data() {
        return {
            cup: true,
            three: false,
            nine: false,
        }
    },
    methods: {
        clear: function () {
            this.cup = false
            this.three = false
            this.nine = false
        },
        changeOption: function () {
            let select = document.getElementById('select')
            let index = select.selectedIndex
            let value = select.options[index].value
            this.clear()
            if (value == 'cup') {
                this.cup = true
            } else if (value == 'three') {
                this.three = true
            } else if (value == 'nine') {
                this.nine = true
            }
 
        }
    }
}
</script>

