<template>
    <div id="">
        <div>
            <select name="" id="select" @change="changeOption">
                <option value="transition">过渡 transition</option>
                <option value="loading">加载 loading</option>
            </select>
        </div>
        <div id="transition" v-show="transition">
            <div>
                <h4>按钮</h4>
                <button>Button</button>
            </div>
            <div>
                <h4>手风琴效果</h4>
                <ul id="list">
                    <li class="list-item" v-for="list in lists" v-bind:key="list.id" @click="changeList">{{ list.index }}</li>
                </ul>
            </div>
        </div>    
        <div id="loading" v-show="loading">
            <div>
                <h4>Loading A</h4>
                <div class="parant">
                    <div class="loading-one"></div>
                </div>
                <div class="parant">
                    <div class="loading-two"></div>
                </div>
                <div class="parant">
                    <div class="loading-three"></div>
                </div>

                <h4>Loading B</h4>
                <div class="loading-four">
                    <div class="dot-one"></div>
                    <div class="dot-two"></div>
                    <div class="dot-three"></div>
                    <div class="dot-four"></div>
                </div>

                <div class="loading-five"> 
                    <div class="white-circle"></div>
                </div>
                
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Animation',
    data() {
        return {
            transition: true,
            loading: false,
            lists: [
                { index: '' },
                { index: '' },
                { index: '' }]
        }
    },
    methods: {
        clear: function () {
            this.transition = false
            this.loading = false
        },
        changeOption: function () {
            let select = document.getElementById('select')
            let index = select.selectedIndex
            let value = select.options[index].value
            this.clear()
            if (value == 'transition') {
                this.transition = true
            } else if (value == 'loading') {
                this.loading = true
            } 
        },
        changeList: function (e) {
            let arr = document.getElementsByClassName('list-item')
            for(let i = 0; i < arr.length; i++) {
                arr[i].classList.remove('li-hover')
            }
            e.target.classList.add('class', 'li-hover')   
        }
    }
}
</script>

